.Home-page {
  display: flex;
  justify-content: space-between;
  width: 100%;

  .left {
    width: 55%;
    margin: 0 auto;
    margin-top: 24px;

    .top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 24px;

      &-left {
        &__title {
          font-style: normal;
          font-weight: 600;
          font-size: 28px;
          line-height: 140%;
          color: #FFFFFF;
          margin-bottom: 4px;
        }

        &__date {
          font-style: normal;
          font-weight: normal;
          font-size: 16px;
          line-height: 140%;
          color: #E0E6E9;
        }
      }

      &-right {
        label {
          background: #2D303E;
          border: 1px solid #393C49;
          box-sizing: border-box;
          border-radius: 8px;
          padding: 14px;
          display: flex;
          align-items: center;

          img {
            margin-right: 8px;
          }

          input {
            background-color: transparent;
            outline: none;
            border: none;
            color: #fff;
          }
        }
      }
    }

    .middle {
      .menu {
        display: flex;
        align-items: center;

        &-links {
          display: block;
          text-decoration: none;
          color: #fff;
          font-style: normal;
          font-weight: 600;
          font-size: 20px;
          line-height: 140%;
          padding: 13px 32px 13px 0;
          position: relative;
          margin-bottom: 2px;

          &::before {
            content: "";
            position: absolute;
            background-color: transparent;
            top: 100%;
            bottom: 0;
            left: 0;
            right: 100%;
            width: 37px;
            height: 3px;
          }

          &.active {
            color: #EA7C69;

            &::before {
              background-color: #EA7C69;
            }
          }
        }
      }

      border-bottom: 1px solid #fff;
      margin-bottom: 24px;
    }

    &-bottom {
      &-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 58px;

        &__title {
          color: #fff;
          font-style: normal;
          font-weight: 600;
          font-size: 20px;
          line-height: 140%;
          color: #FFFFFF;
        }

        &__select {
          background: #1F1D2B;
          border: 1px solid #393C49;
          box-sizing: border-box;
          border-radius: 8px;
          padding: 15px;
          color: #fff;
        }

      }

      &-bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        overflow-y: scroll;
        height: 69vh;

        &::-webkit-scrollbar {
          display: none;
        }
      }
    }

  }

  .right {
    background: #1F1D2B;
    border-radius: 8px;
    width: 40%;
    padding: 24px;
    box-sizing: border-box;

    &-title{
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 140%;
      color: #FFFFFF;
      margin-bottom: 24px;
    }

    .button-group{
      margin-bottom: 24px;

      .order-btn{
        border: none;
        margin: 0 4px;
        color: #EA7C69;
        background: #1F1D2B;
        border: 1px solid #393C49;
        box-sizing: border-box;
        border-radius: 8px;
        padding: 7px 12px;
        margin-bottom: 24px;

        &.active{
          color: #fff;
          background: #EA7C69;
        }
      }
    }

    .heading{
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #393C49;
      margin-bottom: 24px;

      .title{
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 140%;
        color: #FFFFFF;
      }

      .right{
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
    }

    .main-block{
      overflow-y: scroll;
      height: 55vh;
      
      &::-webkit-scrollbar{
        display: none;
      }
    }
  
    .bottom{
      padding-top: 24px;

      .top{
        margin-bottom: 37px;

        &-top{
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 12px;

          .discount{
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            color: #ABBBC2;
          }

          .price{
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            text-align: right;
            color: #FFFFFF;
          }
        }

      &-bottom{
        display: flex;
        align-items: center;
        justify-content: space-between;

        .subTotal{
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 140%;
          color: #ABBBC2;
        }

        .price {
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 140%;
          text-align: right;
          color: #FFFFFF;
        }
      }
    }

    .bottom-btn{
      background: #EA7C69;
      box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.3);
      border-radius: 8px;
      border: none;
      width: 100%;
      padding: 14px;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 140%;
      color: #FAFAFA;
      outline: none;
      cursor: pointer;
    }

    }
  
  }
}