.mencard{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;

  .fcol{
    display: flex;
    align-items: center;
    img{
      margin-right: 16px;
    }

    .name{
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 140%;
      color: #E0E6E9;
    }
  }

  &-menu{
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 140%;
    color: #E0E6E9;
    border: none;
  }
  
  .total-payment{
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 140%;
    color: #E0E6E9;
  }

  .status{
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    color: #50D1AA;
    background: rgba(107, 226, 190, 0.24);
    border-radius: 30px;
    padding: 4px 12px;

    &.preparing{
      color: #9290FE;
      background: rgba(146, 144, 254, 0.2);
      padding: 4px 15.5px;
    }

    &.pending{
      color: #FFB572;
      background: rgba(255, 181, 114, 0.2);
      padding: 4px 20.5px;
    }
  }
}