.card{
  text-decoration: none;
  color: #fff;
  background: #1F1D2B;
  border-radius: 16px;
  text-align: center;
  display: block;
  padding: 24px;
  position: relative;
  margin-bottom: 24px;
  box-sizing: border-box;
  margin-left: 28px;
  margin-right: 28px;
  margin-top: 50px;

  &-img{
    position: relative;
    top: -56px;
  }

  &-title{
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    text-align: center;
    color: #FFFFFF;
    margin-bottom: 8px;
  }

  &-price{
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 140%;
    text-align: center;
    color: #FFFFFF;
    margin-bottom: 4px;
  }

  &-bowl{
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 140%;
    text-align: center;
    color: #ABBBC2;
  }
}