.dash-card{
  background: #1F1D2B;
  border-radius: 8px;
  padding: 16px;
  width: 199px;

  &-top{
    display: flex;
    align-items: center;
    margin-bottom: 8px;

    .percent{
      font-style: normal;
      font-weight: 500;
      font-size: 12px;
      line-height: 140%;
      color: #FF7CA3;
      margin-left: 8px;
      position: relative;
    
      &::after{
        content: url(../../assets/images/danger.svg);
        position: absolute;
        top: 0;
        bottom: 0;
        right: -30px;
        left: auto;
      }
    
      &.success{
        color: #50D1AA;

        &::after{
          content: url(../../assets/images/success.svg);
        }
      }
    }
  }

  .numb{
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 140%;
    color: #FFFFFF;
    margin-bottom: 8px;
  }

  .title{
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    color: #ABBBC2;
  }
}