.dash-right-card{
  display: flex;
  align-items: center;
  margin-bottom: 24px;

  img{
    width: 56px;
    height: 56px;
    margin-right: 16px;
  }

  .info{
    .title{
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 130%;
      color: #E0E6E9;
      margin-bottom: 4px;
    }

    .dishCount{
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 140%;
      color: #ABBBC2;
    }
  }
}