.Dashboard{
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
  padding: 0 24px;
  
  .left{
    width: 60%;

    .top {
      border-bottom: 1px solid #393C49;
      margin-bottom: 24px;

      &-title {
        font-style: normal;
        font-weight: 600;
        font-size: 28px;
        line-height: 140%;
        color: #FFFFFF;
        margin-bottom: 4px;
      }

      &-subtitle {
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 140%;
        color: #ABBBC2;
      }
    }

    .card-block{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 24px;
    }

    .main-block{
      background: #1F1D2B;
      border-radius: 8px;
      padding: 24px;

      .top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: none;

        .title{
          font-style: normal;
          font-weight: 600;
          font-size: 20px;
          line-height: 140%;
          color: #FFFFFF;
        }

        &-btn{
          background: #1F1D2B;
          border: 1px solid #393C49;
          box-sizing: border-box;
          border-radius: 8px;
          color: #fff;
          padding: 13.5px 14px;
        }
      }

      .menu{
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid #393C49;

        .title{
          font-style: normal;
          font-weight: 600;
          font-size: 14px;
          line-height: 140%;
          color: #FFFFFF;
          padding: 16px;
        }
      }

      .block{
        overflow-y: scroll;
        height: 55vh;

        &::-webkit-scrollbar{
          display: none;
        }
      }

    }
  }

  .right{
    width: 40%;
    box-sizing: border-box;
    margin-left: 24px;

    &-top{
      background: #1F1D2B;
      border-radius: 8px;
      padding: 24px;
      margin-bottom: 24px;

      &-top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid #393C49;

        .title{
          font-style: normal;
          font-weight: 600;
          font-size: 20px;
          line-height: 140%;  
          color: #FFFFFF;
        }

        .dayselect{
          background: #1F1D2B;
          border: 1px solid #393C49;
          box-sizing: border-box;
          border-radius: 8px;
          padding: 13.5px;
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 130%;
          color: #FFFFFF;
        }
      }

      .right-top-bottom{
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 140%;
        color: #EA7C69;
        padding: 14px;
        background-color: #1F1D2B;
        border: 1px solid #EA7C69;
        border-radius: 10px;
        width: 100%;
      }
    }

    &-bottom{
      background: #1F1D2B;
      border-radius: 8px;
      padding: 24px;

      &-top{
        border-bottom: 1px solid #393C49;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 24.5px;
        margin-bottom: 32px;

        .title{
          font-style: normal;
          font-weight: 600;
          font-size: 20px;
          line-height: 140%;
          color: #FFFFFF;
        }

        .dayselect {
          background: #1F1D2B;
          border: 1px solid #393C49;
          box-sizing: border-box;
          border-radius: 8px;
          padding: 13.5px;
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 130%;
          color: #FFFFFF;
        }
      }

      &-bottom{
        display: flex;
        align-items: center;
        
        .info{
          margin-left: 54px;

          &-item{
            margin-bottom: 16px;
            position: relative;

            &::before{
              content: "";
              position: absolute;
              top: 0;
              bottom: 0;
              left: -25px;
              right: auto;
              width: 18px;
              height: 18px;
              border-radius: 50%;
              background-color: #FF7CA3;
            }

            &.to-go::before{
              background-color: #FFB572;
            }

            &.delivery::before{
              background-color: #65B0F6;
            }

            &-title{
              font-style: normal;
              font-weight: 500;
              font-size: 14px;
              line-height: 130%;
              color: #FFFFFF;
              margin-bottom: 2px;
            }

            &-subtitle{
              font-style: normal;
              font-weight: normal;
              font-size: 12px;
              line-height: 140%;
              color: #ABBBC2;
            }

          }
        }
      }
    }

  }
}