.RigthSidebar {
  background: #1F1D2B;
  border-radius: 16px 16px 0px 16px;
  height: 100vh;
  padding: 24px;
  box-sizing: border-box;
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  left: auto;


  &.active{
    display: block;
  }

  .top{
    border-bottom: 1px solid #393C49;
    padding-bottom: 24px;
    margin-bottom: 24px;

    .title{
      font-style: normal;
      font-weight: 600;
      font-size: 28px;
      line-height: 140%;
      color: #FFFFFF;
      margin-bottom: 8px;
    }

    .subtitle{
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 140%;
      color: #ABBBC2;
      
    }
  }

  .middle{
    .title{
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 140%;
      color: #FFFFFF;
      margin-bottom: 16px;
    }

    &-block{
      margin-bottom: 16px;

      .btn {
        background: hsl(249, 19%, 14%);
        border: 1px solid #393C49;
        box-sizing: border-box;
        border-radius: 8px;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        color: #ABBBC2;
        padding: 10px 30px;
        outline: none;
        margin-right: 8px;

        &:first-child {
          background: #252836;
          border: 1px solid #ABBBC2;
          position: relative;

          &::after{
            content: "";
            background-image: url(../../assets/images/checkmark-circle.svg);
            background-repeat: no-repeat;
            width: 16px;
            height: 16px;
            position: absolute;
            top: 6px;
            right: 6px;
          }
        }
      }
    }

    .holder{
      form{
        label{
          font-style: normal;
          font-weight: 500;
          font-size: 20px;
          display: block;
          line-height: 130%;
          margin-bottom: 8px;
          color: #FFFFFF;
        }

        .holder-inp{
          display: block;
          width: 100%;
          padding: 14px;
          background: #2D303E;
          color: #E0E6E9;
          border: 1px solid #393C49;
          box-sizing: border-box;
          border-radius: 8px;
          margin-bottom: 16px;
        }

        .middle{
          display: flex;
          align-items: center;
          justify-content: space-between;
          border-bottom: 1px solid #393C49;
        }

        .bottom {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 90px;
        }
      }
    }
  }

  .bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-primary{
      border: 1px solid #EA7C69;
      box-sizing: border-box;
      border-radius: 8px;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 140%;
      color: #EA7C69;
      padding: 14px 34px;
      background-color: #1F1D2B;
      outline: none;
      display: block;
      width: 100%;
      margin-right: 8px;
    }

    &-secondary{
      background: #EA7C69;
      box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.4);
      border-radius: 8px;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 140%;
      color: #FAFAFA;
      padding: 14px 34px;
      outline: none;
      border: none;
      display: block;
      width: 100%;
    }
  }
}