.right-card{
  .top{
    display: flex;
    align-items: center;

    .left{
      display: flex;
      align-items: center;
      margin-bottom: 14px;

      &-img{
        width: 80px;
        height: 80px;
        object-fit: contain;
        margin-right: 8px;
      }

      &-right{
        &-title{
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 130%;
          color: #FFFFFF;
          margin-bottom: 8px;
        }

        &-price{
          font-style: normal;
          font-weight: 500;
          font-size: 12px;
          line-height: 140%;
          color: #ABBBC2;
        }
      }
    }

    .right{
      display: flex;
      align-items: center;
      justify-content: space-between;

      .count{
        background: #2D303E;
        border: 1px solid #393C49;
        box-sizing: border-box;
        border-radius: 8px;
        padding: 14px;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        color: #FFFFFF;
      }

      .all-price{
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        text-align: right;
        color: #FFFFFF;
      }
    }
  }

  .bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-right{
      width: 100%;
      margin-right: 20px;

      .bottom-inp {
        background: #2D303E;
        border: 1px solid #393C49;
        box-sizing: border-box;
        border-radius: 8px;
        padding: 14px;
        width: 100%;
        box-sizing: border-box;
        outline: none;
        color: #fff;
      }
    }

    .trash-btn{
      border: 1px solid #FF7CA3;
      box-sizing: border-box;
      border-radius: 8px;
      background-color: #1F1D2B;
      padding: 14px;
      outline: none;
      cursor: pointer;
    }
  }
}